h4 {
  font-family: Barlow, sans-serif;
  font-size: 9px;
  color: #808080;
}

h5 {
  font-family: Barlow, sans-serif;
  font-size: 75%;
}

#main {
  background: url("./img/A3_Reflection_s3754467.jpg");
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  padding: 0px;
  background-position: center center;
}

.container {
  display: flex;
  flex-flow: column;
}

.container-fluid {
  padding-right: 0px;
  padding-left: 0px;
}

.featured-section {
  position: absolute;
  top: 50%;
  height: 10em;
  margin-top: -10em;
}

.featured-button {
  box-shadow: 6px 6px 0px 0px #ffffff;
  background-color: #303030;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 17px;
  font-weight: bold;
  padding: 17px 29px;
  text-decoration: none;
}

.dashboard-button {
  background-color: transparent;
  border-radius: 15px;
  border: 1px solid #ffffff;
  display: inline-block;
  cursor: pointer;
  color: #333333;
  font-family: Arial;
  font-size: 17px;
  padding: 10px 16px;
  text-decoration: none;
}

.dashboard-button:hover {
  background-color: transparent;
}

.dashboard-button:active {
  position: relative;
  top: 1px;
}

.navlink-right {
  float: right;
}

.list-group-item {
  padding: 2px;
}

.list-text {
  font-size: 18px;
}

@media (max-width: 768px) {
  .bottom-left {
    position: static;
    bottom: 8px;
    left: 16px;
    text-align: left;
    padding-left: 20px;
    padding-bottom: 5px;
  }
}
